import Web3 from 'web3/dist/web3.min.js'
import abi from './abi'

const NODE_PROVIDER_PSC = "https://seed0.PolySmartChain.com"

function createCT(NODE_PROVIDER,contractAddress){
    const web3 = new Web3(NODE_PROVIDER)
    if (abi.contract_abi[contractAddress]){
        const contract = new web3.eth.Contract(abi.contract_abi[contractAddress], contractAddress)
        return contract
    }
    else{
        const contract = new web3.eth.Contract(abi.contract_abi["ERC20"], contractAddress)
        return contract
    }
}
async function getPscChainTotalSuply (contractAddress) { 
    const ct = createCT (NODE_PROVIDER_PSC,contractAddress)
    let ts = await ct.methods.totalSupply().call()
    ts = Web3.utils.fromWei(ts, "ether")
    return ts
 }
async function getPscChainBalanceOfToken(address,contractAddress){
    const ct = createCT (NODE_PROVIDER_PSC,contractAddress)
    let res = await ct.methods.balanceOf(address).call()
    res = Web3.utils.fromWei(res, "ether")
    return res

}


function test(){
    console.log(abi.contract_abi);
}
// contract.methods.totalSupply().call((err, result) => { console.log( web3.utils.fromWei(result, 'ether')) })

export default {
    NODE_PROVIDER_PSC:NODE_PROVIDER_PSC,
    getPscChainTotalSuply:getPscChainTotalSuply,
    getPscChainBalanceOfToken:getPscChainBalanceOfToken,
    test:test
}
