function compareArrDict(property) {
    return function (a, b) {
        var value1 = a[property];
        var value2 = b[property];
        return value1 - value2;
    }
}

function mergeCache(cacheA, cacheB, sortKey) {
    let cacheAD = cacheA.value["data"]
    let cacheBD = cacheB.value["data"]
    cacheAD.push.apply(cacheAD, cacheBD)
    cacheAD.sort(compareArrDict(sortKey))
    return cacheAD
}

function mergeCacheWithTag(cacheA, cacheB, TagA, TagB, sortKey) {
    cacheA.value.forEach((e) => {
        e["tag"] = TagA
    })
    cacheB.value.forEach((e) => {
        e["tag"] = TagB
    })
    const cacheC = cacheA.value.push.apply(cacheA, cacheB)
    cacheC.sort(compareArrDict(sortKey))
    return cacheC
}

export {
    compareArrDict,
    mergeCache
}